export default ".navbar {\n    border-radius: 0%;\n    position: fixed;\n    width: 100%;\n    z-index: 9;\n    font-size: 16px;\n    font-weight: 700;\n    background-color: var(--color-dark);\n    padding-bottom: 10px;\n    min-height: 50px;\n    margin-bottom: 20px;\n    transition: background-color 500ms linear;\n    display: block;\n}\n\n.navbar.scrolled {\n    background-color:var(--color-darker) !important;\n    transition: background-color 500ms linear;\n    box-shadow: 0px 2px 2px 1px rgba(0, 0, 0, 0.18);\n}\n\n.nav-image {\n    margin-left: 0px;\n    height: 24px;\n    width: 24px;\n    opacity: 0.8;\n    transition: opacity 200ms linear;\n}\n\n.nav-image:hover {\n    opacity: 1;\n    transition: opacity 200ms linear;\n}\n\n.main-nav {\n    list-style-type: none;\n    display: none;\n}\n\n.nav-links {\n    text-decoration: none;\n    color: var(--color-light);\n    transition: color 200ms linear;\n}\n\n.nav-links.active {\n    color: white;\n    transition: color 200ms linear;\n}\n\n.logo {\n    text-decoration: none;\n    color: var(--color-light)\n}\n\n.main-nav li {\n    text-align: center;\n    margin: auto;\n}\n\n.logo {\n    display: inline-block;\n    font-size: 22px;\n    margin-top: auto;\n    margin-bottom: auto;\n}\n\n.navbar-toggle {\n    position: absolute;\n    right: 18px;\n    float: right;\n    cursor: pointer;\n    color: var(--color-light-grey);\n    font-size: 24px;\n    margin-top: 0px;\n    transition: color 200ms linear;\n}\n\n.navbar-toggle:hover {\n    color: white;\n    transition: color 200ms linear;\n}\n\n.main-nav.active {\n    display: block;\n}\n\n@media screen and (min-width: 768px) {\n\n    .navbar {\n        z-index: 9;\n        position: fixed;\n        width: 100%;\n        display: flex;\n        justify-content: space-between;\n        padding-bottom: 0;\n        height: 45px;\n        align-items: center;\n    }\n\n    .main-nav {\n        display: flex;\n        margin-right: 30px;\n        flex-direction: row;\n        justify-content: flex-start;\n    }\n\n    .main-nav li {\n        margin: 0;\n    }\n\n    .nav-links {\n        margin-left: 40px;\n        transition: color 200ms linear;\n    }\n\n    .logo {\n        margin-top: 0;\n    }\n\n    .navbar-toggle {\n        display: none;\n    }\n\n    .logo:hover,\n\n    .nav-links:hover {\n        color: var(--color-secondary);\n        transition: color 200ms linear;\n    }\n}\n/*# sourceMappingURL=data:application/json;base64, */";